/*             CONTACT           */
.contact {
  height: calc(100vh - 50px - 131px);
  background-color: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center; }
  .contact h1 {
    color: #edf2f4;
    margin: 0;
    margin-bottom: 1em; }

.submitted {
  color: #edf2f4; }

.contact-form {
  width: 50%;
  height: auto; }
  .contact-form input {
    width: 100%;
    display: block;
    border: 1px solid #8d99ae;
    padding: 10px;
    margin-bottom: 3px; }
  .contact-form textarea {
    padding: 10px;
    width: 100%;
    display: block;
    border: 1px solid #edf2f4; }
  .contact-form button {
    display: block;
    margin-top: .5em;
    float: right;
    border: 1px solid #edf2f4;
    background-color: transparent;
    color: #edf2f4;
    padding: 5px 10px;
    cursor: pointer; }



